/**
 * @noteType component
 * @name DirectiveShow
 * @tagName mt-directive-show
 * @desc show容器
 * @type container|directive
 * @directiveName v-show
 * @wrapClassName mint-directive-show
 * @html
 * <mt-directive-show></mt-directive-show>
 */
export default {
    name: 'mt-directive-show',
    methods: {
        handleClick(evt) {
            this.$emit('click', evt);
        }
    },
    props: {}
};




