export default {
  props: {
    options: { type: Array, default () { return [] } },
    value: { type: Array, default () { return [] } },
    // 是否允许父节点被选中
    parentSelectable: { type: Boolean, default: false }
  },
  computed: {
    currentValue: {
      set (val) {
        this.$emit('input', val)
      },
      get () {
        return JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    handleClick (item) {
      console.log(111)
      this.$emit('item-click', item)
    }
  }
}



